import React from "react";
import "./About.css";


function About() {
  //  Up To Top Btn
    window.addEventListener("scroll", function(){
      const upToTop = document.querySelector("a.bottom__to__top");
       upToTop.classList.toggle("active", window.scrollY > 0)
    });
  return (
    <div className="about component__space" id="About">
      <div className="container">
        <div className="row">
          <div className="col__2">
            <h1 className="about__heading">About Me</h1>
            <div className="about__meta">
              <p className="about__text p__color">
                I started web development 2-years ago.
                I work well with clients and always strive to completely satisfy all of their needs and project requirements,
                and always love to learn new things.
              </p>
              <p className="about__text p__color">
                My primary goal is to provide complete satisfaction to my client's expectations with my experience 
                and diligent worth ethic.
              </p>
              <p className="about__text p__color">
                My core skills are:
                HTML, CSS, JavaScript, React, TypeScript, Bootstrap, Material UI, Styled Component, Firebase.
              </p>
              <div className="about__button d__flex align__items__center">
                <a href="#Contact">
                  <button className="about btn pointer">Download Cv</button>
                </a>
                <a href="#Contact">
                  <button className="about btn pointer">Hire Me</button>
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
      {/* UP TO TOP BTN */}
      <div className="up__to__top__btn">
        <a href="#Home" className="bottom__to__top">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="16"
          height="16"
          fill="currentColor"
          class="bi bi-chevron-up white"
          viewBox="0 0 16 16"
        >
          <path
            fill-rule="evenodd"
            d="M7.646 4.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1-.708.708L8 5.707l-5.646 5.647a.5.5 0 0 1-.708-.708l6-6z"
          />
        </svg>
        </a>
      </div>
    </div>
  );
}

export default About;
